.nav {
  grid-area: nav;
  background: burlywood;
  padding: 1rem;
}
.list {
  margin: 0;
  padding: 0;
}

.list,
.item {
  list-style-type: none;
}

.item {
  margin: 10px;
}

.item a{
  padding: 10px;
  color: #fff;
  text-decoration: none;
}
.item a.active{
  color: gold;
}